@import "../abstracts/variables";
@import "../abstracts/mixins";
//Transparent background
//Label top of the input or floating 
//Designed for a colored parent background
.dropdown_2 {
    &,
    &--normal,
    &--fail,
    &--success,
    &--warning,
    &--loading {
        select {
            border-style: none;
            width: 100%;
            height: 100%;
            padding: .5rem 1rem;
            background-color: transparent;
        }
        option {
            line-height: 4rem;
            padding: 0 1rem;
            background-color: $color-edulink-primary;
            color: $color-edulink-secondary
        }
    }
    &--fail,
    &--success,
    &--warning,
    &--loading {
        select {
            font-weight: bold;
        }
    }
    &,
    &--normal {
        color: $color-edulink-secondary;
        select {
            color: $color-edulink-secondary;
            &::placeholder {
                color: $color-grey-light;
            }
            &:hover {
                border-bottom-color: $color-edulink-secondary;
            }
        }
    }
    &--loading select {
        color: $color-success-secondary;
        border: solid .2rem $color-success-primary;
    }
    &--fail select {
        color: $color-error-primary;
        border: solid .2rem $color-error-primary;
    }
    &--success select {
        color: $color-success-primary;
        border: solid .2rem $color-success-primary;
    }
    &--warning select {
        color: $color-warning-primary;
        border: solid .2rem $color-warning-primary;
    }
    &__error {
        &--active,
        &--disabled {
            @include transition-type-1;
        }
        &--active {
            width: 100%;
            float: left;
            background-color: $color-error-secondary;
            color: $color-primary-white;
            padding: 1rem;
            position: relative;
        }
        &--disabled {
            width: 0;
            overflow: hidden;
            position: absolute;
        }
    }
    &__comment {
        &--active,
        &--disabled {
            @include transition-type-1;
        }
        &--active {
            width: 100%;
            float: left;
            color: $color-primary-white;
            padding: 1rem 0;
            position: relative;
        }
        &--disabled {
            width: 0;
            overflow: hidden;
            position: absolute;
        }
    }
    &__label {
        display: none;
    }
}