@import "../../abstracts/variables";

.admin {
    &.header {
        box-shadow: $box-shadow-light;
        &--inactive {
            .header {
                height: 10vh;
                position: fixed;
                width: 100%;
                background-color: $color-primary-white;
                z-index: 50;

                &__signup,
                &__signin {
                    width: 15rem;
                    height: 100%;
                    float: right;
                    margin-left: 2rem;
                }

                &__right {
                    float: right;
                    height: 5vh;
                    margin-top: 3vh;
                    padding-right: 5rem;
                }

                &__text {
                    float: right;
                    margin-right: 1rem;
                    color: $color-primary-black;
                    height: 5vh;
                    padding: 1vh 0;
                }

                &__left {
                    float: left;
                    height: 8vh;
                    margin-top: 1vh;
                }

                &__logo {
                    height: 5vh;
                    width: 10vh;
                    margin: 1vh 0 0 15rem;
                    float: left;

                    img {
                        height: 100%;
                        width: 100%;
                    }
                }

                &__title {
                    float: left;
                    color: $color-edulink-primary;
                    margin: 2.5vh 0 0 5rem;
                }
            }
        }

        &--active {
            top: 0;
            position: absolute;
            z-index: 15;

            .header {
                height: 10vh;
                position: fixed;
                width: 100%;
                box-shadow: $box-shadow-light;
                background-color: $color-primary-white;
                z-index: 50;

                &__right {
                    float: right;
                    height: 100%;
                    padding-top: 2.5vh;
                    max-width: 40%;

                    &__text {
                        float: left;
                        margin-right: 1rem;
                        color: $color-primary-black;
                    }

                    &__logOut {
                        width: 15vh;
                        height: 5vh;
                        float: right;
                        margin: 0 2rem;
                    }

                    &__nf {
                        float: right;
                    }

                    .iconBtn--normal {
                        width: 4vh;
                        height: 4vh;
                        float: right;
                        padding: .5vh;
                    }
                }

                &__signup {
                    float: left;
                    color: $color-edulink-primary;
                }

                &__left {
                    float: left;
                    height: 100%;
                    width: 15%;
                }

                &__logo {
                    height: 5vh;
                    width: 10vh;
                    margin: 2rem auto;

                    img {
                        height: 100%;
                        width: 100%;
                    }
                }

                &__title {
                    float: left;
                    color: $color-edulink-primary;
                    line-height: 10vh;
                    padding: 0 2rem;
                    width: 30%;
                    text-transform: capitalize;
                }
            }
        }
    }
}