@import "../../abstracts/mixins";
@import "../../abstracts/variables";
@import "../../components/admin/product";


.adminProductsPage{
    #main {
        height: 40rem;
        margin: 5rem auto;
        position: static;
    }

    .view {

        &--active,
        &--disabled {
            @include transition-type-2;
            background-color: $color-primary-white;
            float: left;
            overflow: hidden;
            position: relative;
            width: 100%;
        }

        &--active {
            height: 100%;
        }

        &--disabled {
            height: 0;
        }

        &--scrollable {
            overflow: scroll;
            width: 100%;
            height: 100%;
        }
    }

    .topBar {
        border-bottom: solid .1rem $color-edulink-primary;
        float: left;
        width: 100%;
        position: relative;

        &__title {
            color: $color-edulink-primary;
            padding: 1rem 2rem;
            float: left;
        }

        &__right {
            height: 5rem;
            padding: 0;
            position: absolute;
            right: 0;

            &__add {
                height: 4rem;
                width: 12rem;
                float: right;
            }

            &__button {
                height: 4rem;
                width: auto;
                float: right;
            }
        }
    }

    .content {
        &__view {
            min-height: 60vh;
            float: left;
            width: 100%;
        }
    }

    .loadBtn {
        margin-top: 2rem;
        float: left;
        width: 100%;
    }

    #addPro {
        &__image {
            width: 60%;
            height: 30rem;
            margin-left: 20%;

            #img_select {
                width: 100%;
                height: 100%;
                position: relative;

                &__img {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    z-index: 0;
                    float: left;
                    overflow: hidden;
                }

                &__buttons {
                    width: 15%;
                    height: 4rem;
                    position: absolute;
                    bottom: 2rem;
                    margin: 0 40%;
                    z-index: 20;
                    float: left;
                    overflow: hidden;
                }
            }
        }

        &__form {
            width: 60%;
            position: relative;
            margin: 5rem 20% 10rem 20%;
            float: left;

            .comment {
                margin-top: 1rem;
            }
        }

        &__title,
        &__author,
        &__textBox,
        &__summaryBox {
            float: left;
            width: 100%;
            margin-bottom: 3rem;

            &__label {
                color: $color-edulink-primary;
                margin: 1rem 0;
            }
        }

        &__title,
        &__author {
            &__input {
                width: 100%;
                height: 3.5rem;
            }
        }

        &__textBox {
            &__input {
                height: auto;
                width: 100%;
                position: relative;
            }
        }

        &__summaryBox {
            textarea {
                height: 15rem;
                color: $color-grey-dark;
                padding: 1rem;
                text-indent: 0 !important;
            }
        }

        &__save,
        &__return {
            float: left;
            position: relative;
            width: 10rem;
            margin-right: 2rem;
        }
    }

    .popUp__content {
        width: 150vh;
        height: 80vh;
    }

    .repo {
        .popup {
            &--active {
                left: 0;
                top: 0;
                height: 100%;
                width: 100%;
            }

            &__content {
                transform: none;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
    }

    .productPage.form {
        &__image {
            width: 60%;
            height: 30rem;
            margin-left: 20%;

            #img_select {
                width: 100%;
                height: 100%;
                position: relative;

                &__img {
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    z-index: 0;
                    float: left;
                    overflow: hidden;
                }

                &__buttons {
                    width: 15%;
                    height: 4rem;
                    position: absolute;
                    bottom: 2rem;
                    margin: 0 40%;
                    z-index: 20;
                    float: left;
                    overflow: hidden;
                }
            }
        }

        &__box {
            position: relative;
            width: 60%;
            margin: 5rem 20% 10rem 20%;
            float: left;
        }

        &__title,
        &__author,
        &__textBox,
        &__summaryBox,
        &__tagInput {
            float: left;
            width: 100%;
            margin-bottom: 3rem;
        }

        &__textBox,
        &__tagInput {
            &__label {
                color: $color-edulink-primary;
                margin-bottom: 1rem;
            }
        }

        &__summaryBox {
            textarea {
                min-height: 5rem;
                max-height: 15rem;
            }
        }

        &__buttons {
            float: left;

            &__button {
                width: 15rem;
                float: left;
                height: 5rem;
                margin-right: 2rem;
            }
        }
    }
}