  @import "../../abstracts/variables";
  
  .pace {
      -webkit-pointer-events: none;
      pointer-events: none;
      -webkit-user-select: none;
      -moz-user-select: none;
      user-select: none;
  }
  
  .pace-inactive {
      display: none;
  }
  
  .pace .pace-progress {
      background: $color-edulink-primary;
      position: fixed;
      z-index: 2000;
      top: 0;
      right: 100%;
      width: 100%;
      height: .4rem;
  }